import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LibroclickedService {

  libros: Array<object>
  constructor() 
  {
    this.libros = 
    [


    ]

  }

  //no tiene el guion bajo porq no proviene de una vista
  libroVisto(librovisto)
  {
    this.libros.push(librovisto)
  }

  verListado()
  {
    if(this.libros.length > 0)
      return this.libros;
    else 
      return false;
  }

}
